import React from 'react';
import ClientsList from '../clients-list';
import Fade from 'react-reveal/Fade';
import './agency-block.css';

const AgencyBlock = ({ description }) => (
    <div className="agency-block">
        <Fade bottom>
            <p>{description ? description : 'Ramotion is a team of multidisciplinary digital product experts focused on branding, UI/UX design, mobile, and web development. We extend design, development and marketing departments of growing startups and Fortune 500 companies. Our San Francisco studio is intentionally small, working with a few clients at a time.'}</p>
            <p>Возможно, одно из лучших креативных агентств в Узбекистане, и уж точно одно из самых награждаемых. Было основано в 2019 году. Сейчас в нашей  компании работает 20 человек, благодаря им Abstract такой классный. Агентство предоставляет продакшн сервис на территории Узбекистана и стран постсоветского пространства.</p>
            <h5>Наши услуги включают:</h5>
            <ul>
                <li>Препродакшн и кастинг</li>
                <li>Подбор локейшенов и организация разрешений</li>
                <li>Аренда оборудования</li>
                <li>Строительство декораций</li>
                <li>Подбор и создание костюмов</li>
                <li>Постпродакшн (компьютерная графика и анимация, создание/подбор музыки)</li>
            </ul>
            <h2>Клиенты</h2>
            <p>За плечами Abstract Visuals много выполненных проектов — рекламных роликов, корпоративных и презентационных фильмов. Мы гордимся своей работой. Мы нацелены на качественный результат. Мы любим то, что делаем. Бизнес этих компаний мы с удовольствием двигаем вперед.</p>
            <ClientsList />
            <h2>Адрес</h2>
            <p>ул. Юсуфа Хос Хожиба, 64, Ташкент 100031 Узбекистан</p>
            <h2>Цена:</h2>
            <p>Каждый проект отличается друг от друга, поэтому мы не можем дать оценку без всех деталей. Заполните наш <a href="https://docs.google.com/forms/d/e/1FAIpQLSfzI3SPOzXYWK6VyQJ0qj4v5UdPFI_BiMQS3Qp1mf4Xx6_2VA/viewform">бриф</a> или получите бесплатную консультацию по телефону <a href="tel:+998712004414">+99871 200 44 14</a>.</p>
            <h2>Социальные сети</h2>
            <p className="agency-socials">
                <a href="http://fb.com/abstractvs">Facebook</a>
                <a href="http://vimeo.com/abstractvs">Vimeo</a>
                <a href="http://instagram.com/abstractvs">Instagram</a>
                <a href="http://youtube.com/abstractvs">YouTube</a>
            </p>
        </Fade>
    </div>
);

export default AgencyBlock;