import React from 'react';
import Layout from '../../components/layout';
import SEO from '../../components/seo';
import MainHeader from '../../components/main-header';
import AgencyBlock from '../../components/agency-block';
import Footer from '../../components/footer';

const title = 'Креативное агентство Abstract Visuals';
const data = { title: "Давайте поговорим о вашем продукте", des: "Должны ли мы общаться?", url: "/ru/contact" };
const description = 'Abstract – это видеопродакшн и больше, мы предоставляем полный цикл от идеи до готового видеопроекта любой сложности. И даже интереснее, когда сложнее. Наши проекты – гордость для нас и наших клиентов.';

const Agency = () => (
    <Layout ru>
        <SEO title="О нас" />
        <section className="container work-block no-margin">
            <MainHeader title={title} />
            <AgencyBlock description={description} />
        </section>
        <Footer data={data} />
    </Layout>
);

export default Agency;